<template>
  <div class="vux-canvas">
    <!--    -->
    <canvas id="canvas"
            ref="canvas"
            :height="canvasHeight" :style="{backgroundColor:bgColor}"
            :width="canvasWidth"

    ></canvas>
    <!--    <div v-if="btnType==='icon'">-->
    <!--      <div style="display: flex;justify-content: space-around;padding: 0 20px">-->
    <!--        &lt;!&ndash;        todo&ndash;&gt;-->
    <!--        <vux-button size="small" @click="eraserCanvas" :type="isEraser?'success':'default'" circle></vux-button>-->
    <!--        <vux-button size="small" @click="revokeCanvas" circle></vux-button>-->
    <!--        <vux-button size="small" @click="antiCancellationCanvas" circle ></vux-button>-->
    <!--        <vux-button size="small" @click="clearCanvas" circle icon="iconfont icon-delete" type="danger"></vux-button>-->
    <!--        <vux-button size="small" @click="exportCanvas" type="primary" circle></vux-button>-->
    <!--      </div>-->
    <!--    </div>-->
    <div v-if="btnType==='text'">
      <vux-button round size="small" type="danger" @click="clearCanvas"></vux-button>
      <vux-button round size="small" type="primary" @click="exportCanvas"></vux-button>
    </div>

  </div>

</template>

<script>

export default {
  name: "vuxCanvas",
  props: {
    //
    bgColor: {
      type: String,
      default: '#f5f7fa'
    },
    //
    canvasWidth: {
      type: Number,
      default: 400
    },
    //
    canvasHeight: {
      type: Number,
      default: 200
    },

    //:
    //todo
    //
    penColor: {
      type: String,
      default: '#000'
    },
    //
    penWidth: {
      type: Number,
      default: 3
    },
    btnType: {
      type: String,
      default: 'text'
    }
  },
  data() {
    return {
      canvasHistory: [],
      step: -1,
      //
      isEraser: false,
      isPenClick: false,
      startPoint: {},
      endPoint: {},
      imgSrc: '',
    }
  },
  mounted() {

    this.init();
  },
  methods: {
    //
    revokeCanvas() {
      if (this.step === -1) {
        this.$msg.warning("")
        return
      }
      //  
      var canvas = document.getElementById("canvas"); //canvas
      var cxt = canvas.getContext("2d");
      if (this.step > 0) {
        this.step--;
        let canvasPic = new Image();
        canvasPic.src = this.canvasHistory[this.step];
        canvasPic.onload = () => {
          cxt.drawImage(canvasPic, 0, 0);
        }


      } else {
        this.$msg.warning("")
        return

      }


    },
    //
    antiCancellationCanvas() {
      const canvas = document.getElementById("canvas"); //canvas
      const cxt = canvas.getContext("2d");
      if (this.step === -1 || this.canvasHistory.length === 0) {
        return
      }
      if (this.step < this.canvasHistory.length - 1) {
        this.step++;
        let canvasPic = new Image();
        canvasPic.src = this.canvasHistory[this.step];
        canvasPic.onload = function () {
          cxt.drawImage(canvasPic, 0, 0);
        }
      }
    },
    eraserCanvas() {
      this.isEraser = !this.isEraser;
      this.isPenClick = false;
    },
    //
    clearCanvas() {
      const canvas = document.getElementById("canvas"); //canvas
      const cxt = canvas.getContext("2d"); // context 
      cxt.fillStyle = this.bgColor; //
      cxt.beginPath();
      cxt.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
      cxt.closePath();
      this.imgSrc = '';
      this.isPenClick = false;
      this.step = 0;
      this.canvasHistory = [];
      this.isEraser = false;


    },
    //
    exportCanvas() {
      const canvas = document.getElementById("canvas"); //canvas
      this.imgSrc = canvas.toDataURL("image/png"); //
      this.$emit('exportImgSrc', this.imgSrc)

    },

    init() {
      const canvas = this.$refs.canvas;
      const cxt = canvas.getContext("2d");
      cxt.fillStyle = this.bgColor; //
      cxt.fillRect(0, 0, this.canvasWidth, this.canvasHeight); // width * height  (0,0)
      canvas.addEventListener("mousemove", this.handleMouseMovement); //
      canvas.addEventListener("mousedown", this.handleMouseDown); //
      canvas.addEventListener("mouseup", this.handleMouseUp); //
      // //
      canvas.addEventListener("touchmove", this.handleMouseMovement); //
      canvas.addEventListener("touchstart", this.handleMouseDown); // 
      canvas.addEventListener("touchend", this.handleMouseUp); // 


    },
    //mouseDown  
    handleMouseDown(e) {
      const canvas = this.$refs.canvas;
      const cxt = canvas.getContext("2d");
      console.log(e)
      this.isPenClick = true;
      // this.$emit('')

      if (e.clientX !== undefined) {
        this.startPoint = {
          x: e.offsetX,
          y: e.offsetY,
        }
        if (this.isEraser) {
          return
        }
        this.drawCircle(this.startPoint.x, this.startPoint.y);
      } else {

        this.startPoint = {
          x: e.changedTouches[0].clientX - canvas.offsetLeft,
          y: e.changedTouches[0].clientY - canvas.offsetTop
        }

        if (this.isEraser) {
          return
        }
        this.drawCircle(this.startPoint.x, this.startPoint.y);

      }


    },
    //MouseMovement
    handleMouseMovement(e) {
      const canvas = document.getElementById("canvas"); //canvas
      const cxt = canvas.getContext("2d");

      if (this.isPenClick) {
        if (e.clientX !== undefined) {
          this.endPoint = {
            x: e.offsetX,
            y: e.offsetY,
          }
        } else {
          this.endPoint = {
            x: e.changedTouches[0].clientX - canvas.offsetLeft,
            y: e.changedTouches[0].clientY - canvas.offsetTop
          }
        }
        this.drawLine(cxt)
      }
      //
      this.startPoint = this.endPoint;


    },


    //mouseUp
    handleMouseUp() {
      this.isPenClick = false;
      this.recordOperation();
      this.startPoint.x = undefined;
      this.startPoint.y = undefined;

    },
    drawLine(cxt) {
      //
      cxt.lineCap = "round";
      cxt.lineCap = "round";
      //
      if (this.isEraser) {
        cxt.strokeStyle = this.bgColor;
        //
        cxt.lineWidth = this.penWidth * 3;

      } else {
        cxt.strokeStyle = this.penColor;
        cxt.lineWidth = this.penWidth;


      }
      cxt.beginPath();
      cxt.moveTo(this.startPoint.x, this.startPoint.y)
      cxt.lineTo(this.endPoint.x, this.endPoint.y);
      cxt.stroke();
    },
    recordOperation() {
      if (this.isEraser) {
        return
      }
      const canvas = document.getElementById("canvas"); //canvas
      this.step++;
      if (this.step < this.canvasHistory.length) {//
        this.canvasHistory.length = this.step;
      }
      // 
      this.canvasHistory.push(canvas.toDataURL());
    },
    //,
    drawCircle(x, y) {
      const canvas = document.getElementById("canvas"); //canvas
      const cxt = canvas.getContext("2d");
      //
      cxt.lineCap = "round";
      cxt.lineCap = "round";
      //
      if (this.isEraser) {
        cxt.strokeStyle = this.bgColor;
        //
        cxt.lineWidth = this.penWidth * 3;

      } else {
        cxt.strokeStyle = this.penColor;
        cxt.lineWidth = this.penWidth;


      }

      // 
      cxt.beginPath();
      // x,yradius
      // startAngleendAngleanticlockwise
      //
      cxt.arc(x, y, 0.1, 0, Math.PI * 2);
      // 
      cxt.fill();
      // 
      cxt.closePath();
      cxt.stroke();

    },


  },

  deactivated() {
    const canvas = document.getElementById("canvas"); //canvas
    canvas.removeEventListener("mousemove", this.handleMouseMovement); //
    canvas.removeEventListener("mousedown", this.handleMouseDown); //
    canvas.removeEventListener("mouseup", this.handleMouseUp); //
    // //
    canvas.removeEventListener("touchmove", this.handleMouseMovement); //
    canvas.removeEventListener("touchstart", this.handleMouseDown); // 
    canvas.removeEventListener("touchend", this.handleMouseUp); // 
  }


}
</script>

<style lang="less" scoped>


</style>
