<template>
  <div>
    <p></p>
    <vux-checkbox v-model="checkValue"></vux-checkbox>
    <p></p>
    <vux-checkbox v-model="checkValue" shape="square"></vux-checkbox>
    <p></p>
    <vux-space direction="vertical">
      <vux-checkbox v-model="falseValue" disabled></vux-checkbox>
      <vux-checkbox v-model="checkValue" disabled></vux-checkbox>
    </vux-space>
    <p></p>
    <vux-checkbox v-model="checkValue" labelDisabled></vux-checkbox>
    <p></p>
    <vux-checkbox-group v-model="groupVal">
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
    </vux-checkbox-group>
    <p></p>
    <vux-checkbox-group v-model="groupVal" direction="horizontal">
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
    </vux-checkbox-group>
    <p></p>
    <vux-checkbox-group v-model="groupVal1" :max="2">
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
    </vux-checkbox-group>
    <p></p>
    <vux-checkbox-group ref="group" v-model="groupVal2">
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
      <vux-checkbox name=""></vux-checkbox>
    </vux-checkbox-group>
    <vux-space style="margin-top: 16px">
      <vux-button type="primary" @click="handleAll"></vux-button>
      <vux-button type="danger" @click="handleNoAll"></vux-button>
    </vux-space>

  </div>
</template>

<script>
export default {
  name: "checkBox",
  data() {
    return {
      checkValue: true,
      falseValue: false,

      groupVal: [""],
      groupVal1: [""],
      groupVal2: [""]
    }
  },
  components: {},
  methods: {
    handleAll() {
      this.$refs.group.toggleAll(true)
    },
    handleNoAll() {
      this.$refs.group.toggleAll()
    }
  }

}
</script>

