<template>
  <transition name="vux_message-fade" @after-leave="onAL">
    <div v-show="show" :class="[type]" :style="controlTop" class="vux_message">
      {{ message }}
    </div>
  </transition>
</template>

<script>
export default {
  name: "VuxMessage",
  // message		string / VNode		
// type		string	success/warning/info/error	info
// iconClass	 type	string		
// dangerouslyUseHTMLString	 message  HTML 	boolean		false
// customClass		string		
// duration	,  0 	number		3000
// showClose		boolean		false
// center		boolean		false
// onClose	,  message 	function		
// offset	Message 	number		20

  data() {
    return {
      message: "",
      type: 'success',
      duration: 3000,
      center: true,
      onClose: null,
      offset: 20,
      show: false,
      timer: null,
    }
  },
  props: {

    onClose: {
      type: Function,
      default: null
    }
  },
  mounted() {
    this.start()
  },

  watch: {
    show(newVal) {
      // if (!newVal) {
      //   this.onAL()
      // }
    }
  },
  computed: {
    controlTop() {
      return {
        // message
        top: `${this.offset}px`,
      };
    },
  },
  methods: {
    start() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          this.close()
        }, this.duration)
      }
    },
    clear() {
      clearTimeout(this.timer)
    },
    close() {
      this.show = false;
    },
    onAL() {
      this.onClose();
      this.$destroy(true);
      this.$el.parentNode.removeChild(this.$el);
    }
  },

}
</script>

<style lang="less" scoped>

.vux_message {
  min-width: 120px;
  height: auto; // 
  padding: 16px; // 
  height: 32px;
  line-height: 32px;
  text-align: left;
  padding: 0 8px;
  border-radius: 4px;
  position: fixed;
  left: 50%;
  top: 20px;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  color: #333;
  // 
  transition: opacity 0.3s, transform 0.4s, top 0.4s;
  font-size: 13px;

  &.success {
    border: 1px solid #aee884;
    background-color: #f5ffea;
  }

  &.info {
    border: 1px solid #86cfff;
    background-color: #e2f6ff;
  }

  &.warning {
    border: 1px solid #ffe38c;
    background-color: #fffae2;
  }

  &.error {
    background-color: #fff0ee;
    border: 1px solid #ffccc7;
  }


}

.vux-message-fade-enter,
.vux-message-fade-leave-active {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%);
  transform: translate(-50%, -100%);
}
</style>