<template>
  <div>
    <p></p>
    <vux-tree-select v-model="index" :options="options">

    </vux-tree-select>


    <p>
      


    </p>
    <vux-tree-select v-model="activeIndex1" :activeId.sync="activeId" :options="options">

    </vux-tree-select>

    <p>
      
    </p>


    <vux-tree-select v-model="activeIndex" :options="items">
      <template #content>
        <img
            v-if="activeIndex === 0"
            src="https://fastly.jsdelivr.net/npm/@vant/assets/apple-1.jpeg"
        />
        <img

            v-if="activeIndex === 1"
            src="https://fastly.jsdelivr.net/npm/@vant/assets/apple-2.jpeg"
        />
      </template>
    </vux-tree-select>


  </div>

</template>

<script>
const areaList = {
  provinces: {
    110000: '',
    440000: '',
  },
  cities: {
    110100: '',
    440100: '',
    440200: '',
    440300: '',
    440400: '',
    440500: '',
    440600: '',
  },
  counties: {
    110101: '',
    110102: '',
    110105: '',
    110106: '',
    110107: '',
    110108: '',
    110109: '',
    110111: '',
    110112: '',
    110113: '',
    110114: '',
    110115: '',
    110116: '',
    110117: '',
    110118: '',
    110119: '',
    440103: '',
    440104: '',
    440105: '',
    440106: '',
    440111: '',
    440112: '',
    440113: '',
    440114: '',
    440115: '',
    440117: '',
    440118: '',
    440203: '',
    440204: '',
    440205: '',
    440222: '',
    440224: '',
    440229: '',
    440232: '',
    440233: '',
    440281: '',
    440282: '',
    440303: '',
    440304: '',
    440305: '',
    440306: '',
    440307: '',
    440308: '',
    440309: '',
    440310: '',
    440311: '',
    440402: '',
    440403: '',
    440404: '',
    440507: '',
    440511: '',
    440512: '',
    440513: '',
    440514: '',
    440515: '',
    440523: '',
    440604: '',
    440605: '',
    440606: '',
    440607: '',
    440608: '',
  },
};

export default {
  name: "TreeSelect",
  data() {
    return {
      index: 1,
      activeIndex: 0,
      activeIndex1: 0,
      activeId: [],
      items: [{text: ' 1'}, {text: ' 2'}],
      citys: [],
      options: [
        {
          text: '',
          children: [
            {text: '', id: 1},
            {text: '', id: 2},
            {text: '', id: 3, disabled: true},
          ],
        },
        {
          text: '',
          children: [
            {text: '', id: 4},
            {text: '', id: 5},
            {text: '', id: 6},
          ],
        },
        {text: '', disabled: true},
      ],
    }
  },
  components: {},
  created() {
    this.citys = this.generateTree();
    console.log(this.citys)
  },
  methods: {
    generateTree() {
      const {provinces, cities, counties} = areaList;
      const options = [];
      const eachObj = (obj, cb) => Object.keys(obj).forEach(cb);
      const match = (v1, v2, base) => parseInt(v1 / base, 10) === parseInt(v2 / base, 10);
      eachObj(provinces, (prov) => {
        const cityList = [];

        eachObj(cities, (city) => {
          const countyList = [];
          if (match(city, prov, 10000)) {
            eachObj(counties, (county) => {
              if (match(county, city, 100)) {
                countyList.push({
                  label: counties[county],
                  value: county,
                });
              }
            });
            cityList.push({
              label: cities[city],
              text: city,
              children: countyList,
            });
          }
        });
        const item = {
          label: provinces[prov],
          value: prov,
          children: cityList,
        };

        options.push(item);
      });

      return options;
    },

  }

}
</script>

