<template>
  <div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "vuxCollapse",
  data() {
    return {
      // v-model
      activeNames: this.value, // 
    };
  },
  props: {
    //
    accordion: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Array,
      default: () => []
    },
    defaultExpandAll: {
      type: Boolean,
      default: false
    },
    showOperation: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    defaultExpandAll(val) {
      if (val) {
        this.expandAndCollapse(val)
      } else {
        this.expandAndCollapse(false)
      }
    },

    // propsvalue
    value(value) {
      this.activeNames = value;
    },
  },
  mounted() {
    if (this.defaultExpandAll) {
      this.expandAndCollapse(this.defaultExpandAll)
    }

  },

  methods: {
    expandAndCollapse(val) {
      this.$nextTick(() => {
        this.$children.forEach(item => {
          item.isOpen = val;
        })
      })
    },
    updateParent(name, isOpen, vNode) {
      // 
      if (this.accordion && isOpen) {
        this.closeOther(name)
        this.activeNames = [name]
      } else {
        if (!this.activeNames.includes(name)) {
          this.activeNames.push(name);
        } else {
          this.activeNames = this.activeNames.filter(it => it !== name)
        }
      }
      this.$emit("input", this.activeNames);
      this.$emit("change", this.activeNames);
    },
    closeOther(name) {

      this.$children.forEach((item) => {
        // false
        if (item.name !== name) {
          item.isOpen = false;
        }
      });
    },
  }
}
</script>

